.btn-menu {
    background: none;
    width: 30px;
    height: 19px;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    cursor: pointer;
    outline: none;
    position: absolute;
    padding: 0;
    right: 1.25rem;
    top: 1.25rem;
    z-index: 200;
    text-indent: -999em
}

.btn-menu span,
.btn-menu span:after,
.btn-menu span:before {
    cursor: pointer;
    width: 30px;
    height: 3px
}

.btn-menu span {
    display: block;
    position: relative;
    background: #fff;
    transition: all 0ms .3s
}

.btn-menu span:before {
    bottom: 8px;
    transition: bottom .3s cubic-bezier(.23, 1, .32, 1) .3s, transform .3s cubic-bezier(.23, 1, .32, 1)
}

.btn-menu span:after,
.btn-menu span:before {
    content: "";
    position: absolute;
    left: 0;
    background: #fff
}

.btn-menu span:after {
    top: 8px;
    transition: top .3s cubic-bezier(.23, 1, .32, 1) .3s, transform .3s cubic-bezier(.23, 1, .32, 1)
}

.menu-opened .btn-menu span {
    background: hsla(0, 0%, 100%, 0)
}

.menu-opened .btn-menu span:after {
    background: #000;
    top: 0;
    transform: rotate(45deg);
    transition: top .3s cubic-bezier(.23, 1, .32, 1), transform .3s cubic-bezier(.23, 1, .32, 1) .3s
}

.menu-opened .btn-menu span:before {
    background: #000;
    bottom: 0;
    transform: rotate(-45deg);
    transition: bottom .3s cubic-bezier(.23, 1, .32, 1), transform .3s cubic-bezier(.23, 1, .32, 1) .3s
}

